* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  height: 100vh;
  background: rgb(70, 82, 157);
  background: linear-gradient(164deg, rgb(61, 70, 128) 0%, rgb(31, 38, 80) 100%);
}

h3 {
  margin: 10px 0;
}

li {
  margin: 10px;
}

ul {
  list-style: none;
}

input {
  font-size: 15px;
  padding: 10px;
  text-align: center;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
}

.cards {
  display: flex;
  margin: 10px auto;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
}

.todo-card {
  margin: 50px 10px;
  min-height: 300px;
  min-width: 250px;
  background-color: #e1f1ff;
  display: flex;
  flex-direction: column;
  text-align: center;
  border: none;
  border-radius: 5px;
  box-shadow: 0 8px 6px -6px black;
}

.create-row {
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-button {
  background: #2EBAEE;
  font-size: 16px;
  font-weight: bold;
  border-radius: 3px;
  border: none;
  color: white;
  margin: 10px;
  padding: 9px 12px;
  cursor: pointer;
}

.create-card {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.create-todo {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.select {
  margin-left: 10px;
  width: 250px;
  margin-right: 15px;
}

.delete-button {
  color: #2EBAEE;
  cursor: pointer;
}

.todo-points {
  padding: 10px 0;
  background: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.todo-points-text {
  overflow-wrap: break-word;
  width: 70%;
}